            `On 14 June, 1979, his 33rd birthday, Zahir died in mysterious circumstances (officially a car crash, but some have questioned that). On hearing the news, his pregnant wife Fahira went into premature labour, giving birth to a baby girl, Shabnam. Nearly 40 years later, Shabnam and US film director Sam French are collaborating on a Kickstarter-funded documentary about her fathers life.`
